<template>
    <div class="noCheck">
        <div class="content">
            <img src="../img/sorry.png" width="100%" alt="">
            <p class="tips">很抱歉，导师没有选择您</p>
            <button type="button" class="checkTeach" @click="checkTeach">选择其他导师</button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'noCheck',
    methods: {
        checkTeach () {
            this.$router.push({path: '/user/fyCheckMentor'})
        }
    }
}
</script>

<style scoped>
.noCheck {
    width: 100%;
    height: 100%;
    background: #fff;
}
.content {
    width: 100%;
    height: 100%;
    text-align: center;
    padding: 50px;
    box-sizing: border-box;
}
.tips {
    padding: 20px;
    font-size: 16px;

}
.checkTeach {
    padding: 6px 30px;
    color: #fff;
    background: #1989fa;
    border: none;
    outline: none;
    border-radius: 4px;
    font-size: 16px;
}
</style>



