<template>
    <div class="myPanel">
        <div class="panel-header">
            <div class="panel-title">
                <div class="icon">
                    <img src="./img/装饰线条.png" srcset="./img/装饰线条@2x.png 2x, ./img/装饰线条@3x.png 3x">
                </div>
                <div>
                    <p v-text="title"></p>
                </div>
            </div>
        </div>
        <div class="panel-content">
            <slot>
                内容
            </slot>
        </div>
    </div>
</template>

<script>
export default {
    name: 'myPanel',
    props: {
        title: {
            type: String,
            default: '默认标题'
        },
        icon: {
            type: String,
            default: ''
        }
    },
    data () {
        return {
            msg: '默认按钮'
        }
    }
}
</script>

<style scoped>
.myPanel {
    margin-top: 10px;
    background: #f5f5f5;
}
.panel-title {
    box-sizing: border-box;
    padding: 8px 12px;
    display: flex;
    align-items: center;
    background: #fafafa;
    font-size: 16px;
}
.icon {
    margin-right: 10px;
}
.panel-content {
    background: #fff;
    font-size: 14px;
    padding: 10px;
}
</style>



