<template>
    <div class="reason" v-if="reasonFn.isShow">
        <div class="doail">
            <div class="content">
                <div class="header">
                    <h1 class="title">选择导师的理由</h1>
                </div>
                <div class="body">
                    <textarea name="reason" v-model="val"></textarea>
                </div>
                <div class="footer">
                    <button type="button" class="btn" @click="submit">提交</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {Toast} from 'vant'
import tool from 'tool/tool'
export default {
    name: 'reason',
    props: ['reasonFn'],
    data() {
        return {
            val: ''
        }
    },
    methods: {
        submit () {
            if (this.val.length != 0) {
                var obj = {
                    stuOpenID: tool.getUrlKey('stuOpenID'),
                    teaOpenID: tool.getUrlKey('teaOpenID'),
                    reason: this.val,
                    isShow: false
                }
                this.$emit('submitCheck', obj)
            } else {
                Toast('请填写选择导师的理由')
            }
            
        }
    },
}
</script>

<style scoped>
.reason {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.55);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
   
}
.doail {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.content {
    width: 90%;
    background: #fff;
    padding: 10px 15px;
    box-sizing: border-box;
    border-radius: 10px;
}
.header {
    text-align: center;
    box-sizing: border-box;
}
.header .title {
    font-size: 16px;
    margin: 15px 0px;
}
.body textarea[name="reason"] {
    width: 100%;
    height: 150px;
    font-size: 16px;
    font-family: 'Courier New', Courier, monospace;
    box-sizing: border-box;
    padding: 6px 12px;
    background: #ddd;
    border: none;
    outline: none;
    resize: none;
}
.footer {
    width: 100%;
    padding: 20px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
}
.footer .btn {
    background: #dcb676;
    border: none;
    color: #fff;
    width: 35%;
    padding: 10px;
    border-radius: 5px;
    outline: none;
}
</style>



