<template>
    <div class="upFiles">
        <div class="imgShow">
            <h1>图片预览</h1>
            <img :src="imgPath" alt="" width="100%" height="100%">
        </div>
        <form>
            <input type="file" name="stuFile" @change="previewFiles($event)" multiple accept="image/*">
            <button type="button" @click="upLoadImg">提交</button>
        </form>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'upFiles',
    data () {
        return {
            imgPath: '', 
            base64: '',
        }
    },
    methods: {
        previewFiles (e) {
            let files = e.target.files
            let reader = new FileReader()
            // 判断图片大小
            if (files[0].size/1024/1024 > 10) {
                alert('图片太大，请上传小于10M的图片')
                return
            }
            // 读取图片
            reader.readAsDataURL(files[0])
            // 读取完成后
            reader.onloadend = (e) => {
                this.imgPath = e.target.result
                this.base64 = reader.result
            }
            // 上传
            let formData = new FormData()
            formData.append('teaOpenID', 'test')
            formData.append('pic', files[0])
            axios.post('/upTeacherCard', formData, {
                headers: {
                    'Content-Type':'multipart/form-data'
                }
            }).then((res) => {
                console.log(res)
            })
        },
        upLoadImg () {
            let formData = new FormData()
            formData.append('openID', '123')
            formData.append('stuFile', this.base64)
            axios.post('/fyApi/upStuInfo', formData, {
                headers: {
                    'Content-Type':'multipart/form-data'
                }
            }).then((res) => {
                console.log(res)
            })
        }   
    }
}
</script>

<style scoped>
.imgShow {
    width: 520px;
    height: 520px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
}
</style>


