import Vue from 'vue'
import Router from 'vue-router'
// 会员付款页
import fyPay from 'user/fyPay/fyPay'
// 会员信息登记
import fyInfoReg from 'user/fyInfoReg/fyInfoReg'
// 会员群
import fyQRCode from 'user/fyQRCode/fyQRCode'
// 导师选择
import fyCheckMentor from 'user/fyCheckMentor/fyCheckMentor'
// 导师详情
import teacherDetail from 'user/fyCheckMentor/page/teacherDetail'
// 已经选择导师
import checkedTeacher from 'user/fyCheckMentor/page/check'
// 选择导师二维码群
import qrCode from 'user/fyCheckMentor/page/qrCode'
// 导师没有选择
import noCheck from 'user/fyCheckMentor/page/noCheck'
// 评分
import fyScore from 'user/fyScore/fyScore'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/user/fyPay'
    },
    {
      path: '/user/fyPay',
      name: 'fyPay',
      component: fyPay,
      meta: {
        title: '赋羽青年'
      },
      query: {
        openID: 987654321
      }
    },
    {
      path: '/user/fyInfoReg',
      name: 'fyInfoReg',
      component: fyInfoReg,
      meta: {
        title: '加入赋羽'
      }
    },
    {
      path: '/user/fyQRCode',
      name: 'fyQRCode',
      component: fyQRCode,
      meta: {
        title: '赋羽青年'
      }
    },
    {
      path: '/user/fyCheckMentor',
      name: 'fyCheckMentor',
      component: fyCheckMentor,
      meta: {
        title: '导师选择'
      }
    },
    {
      path: '/user/teacherDetail',
      name: 'teacherDetail',
      component: teacherDetail,
      meta: {
        title: '导师详情'
      }
    },
    {
      path: '/user/checkedTeacher',
      name: 'checkedTeacher',
      component: checkedTeacher,
      meta: {
        title: '赋羽青年'
      }
    },
    {
      path:'/user/qrCode',component: qrCode, name: 'qrCode', meta: {title:'赋羽青年'}
    },
    {
      path:'/user/noCheck',component: noCheck, name: 'noCheck', meta: {title:'赋羽青年'}
    },
    {
      path: '/user/fyScore',
      name: 'fyScore',
      component: fyScore,
      meta: {
        title: '赋羽青年'
      }
    },
  ]
})



