<template>
    <div class="fyMentorDetail">
        <div class="banner-img">
            <img src="../img/detail/导师详情图.png" srcset="../img/detail/导师详情图@2x.png 2x, ../img/detail/导师详情图@3x.png 3x" width="100%" alt="">
        </div>
        <div class="detail-info">
            <div class="left">
                <i class="icon icon-left"></i>
            </div>
            <div class="info">
                <h1>心里有期待，然后默默努力着，那件叫做“以后的事”，就会开出花来。</h1>
                <hr>
                <p style="font-size:14px;">采访者：何妹纸 受访者：帮剑</p>
            </div>
            <div class="right">
                <i class="icon icon-right"></i>
            </div>
        </div>
        <div class="detail-content">
            <p class="txt-1">
                此刻正在看这篇专访的小伙伴们，你们是幸福的！
            </p>
            <p style="font-size:14px;">
                导师：{{this.$route.query.teaOpenID}}
            </p>
        </div>
        <div class="btn-group">
            <button type="button" @click="checkNow">立刻选择导师</button>
        </div>
        <reason :reasonFn="reasonFn" @submitCheck="submitCheck"></reason>
    </div>
</template>

<script>
import reason from './reason'
import {Toast} from 'vant'
export default {
    name: 'fyMentorDetail',
    components: {
        reason
    },
    data() {
        return {
            reasonFn: {
                isShow: false
            }
        }
    },
    methods: {
        checkNow () {
            this.reasonFn.isShow = true
        },
        submitCheck (obj) {
            this.reasonFn.isShow = obj.isShow
            var paramsObj = {
                stuOpenID: obj.stuOpenID,
                teaOpenID: obj.teaOpenID,
                reason: obj.reason
            }
            var params = new URLSearchParams
            params.append('stuOpenID', obj.stuOpenID)
            params.append('teaOpenID', obj.teaOpenID)
            params.append('reason', obj.reason)
            this.$server.chooseTeacher(params).then((res) => {
                console.log(res)
                if (res == 'ok') {
                    const toast = Toast.loading({
                        duration: 0,       // 持续展示 toast
                        forbidClick: true, // 禁用背景点击
                        loadingType: 'spinner',
                        message: '导师选择成功，正在为您跳转'
                    });

                    let second = 2;
                    const timer = setInterval(() => {
                        second--;
                        if (second) {
                            
                        } else {
                            clearInterval(timer);
                            Toast.clear();
                            this.$router.push({path: '/user/checkedTeacher', query: {stuOpenID: obj.stuOpenID, teaOpenID: obj.teaOpenID}})
                        }
                    }, 1000)
                } else {
                    Toast('选择失败,优惠券不足')
                }
            })
        }
    },
}
</script>

<style scoped>
.fyMentorDetail {
    width: 100%;
    height: 100%;
    background: #fff;
    padding: 10px 10px 60px 10px;
    box-sizing: border-box;
    background: #fff;
}
.btn-group {
    width: 100%;
    height: 40px;
    position: fixed;
    bottom: 0;
    left: 0;
}
.btn-group button {
    width: 100%;
    height: 100%;
    background: #dcb676;
    color: #6c5329;
    border: none;
    font-size: 14px;
}
.detail-info {
    padding: 15px 10px;
    box-sizing: border-box;
    background: #f5f5f5;
    margin-top: 20px;
}
.info {
    padding: 10px 15px;
    box-sizing: border-box;
    line-height: 1.5;
}
.info h1 {
    font-size: 18px;
}
.info p {
    color: #999;
}
.left {
    display: flex;
}
.right {
    display: flex;
    justify-content: flex-end;
}
.icon {
    width: 20px;
    height: 20px;
}
.icon-left {
    background: url('../img/icon/右上角箭头.png') no-repeat;
}
.icon-right {
    background: url('../img/icon/左下角箭头.png') no-repeat;
}
.detail-content {
    padding-top: 15px;
    box-sizing: border-box;
    line-height: 1.5;
}
.detail-content .txt-1 {
    font-size: 14px;
    font-weight: 500;
}
</style>



