<template>
    <div class="fyQRCode">
        <div class="content">
            <div class="title">
                <h1>{{qrCode.title}}</h1>
            </div>
            <div class="qrCodeImg">
                <img :src="'img/'+qrCode.qrCode" alt="" width="100%">
            </div>
            <div class="tips">
                <p>长按二维码进入私塾</p>
            </div>
        </div>
        <div class="footer">
            <div class="remark-info">
                <p>{{qrCode.remark}}</p>
            </div>
        </div>
    </div>
</template>

<script>
import { Lazyload } from 'vant'
export default {
    name: 'fyQRCode',
    data() {
        return {
            qrCode:{}
        }
    },
    created () {
        // 获取会员群二维码
        var paramsObj = {  
            id: 1, // 会员群id
            flag: 0, // 会员群分类 id
        } 
        this.$server.getQRCodeList(paramsObj).then((res) => {
            if (res) {
                this.qrCode = res[0]
            }
        })

    },
    methods: {
    }
}
</script>

<style scoped>
.fyQRCode {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.content {
    width: 80%;
    background: #fff;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-sizing: border-box;
    padding: 10px 20px;
}
.content .title {
    box-sizing: border-box;
    padding: 20px;
}
.content .tips {
    box-sizing: border-box;
    padding: 20px;
    font-size: 15px;
}
.remark-info {
    box-sizing: border-box;
    padding: 40px 0px;
    font-size: 14px;
}
</style>



