<template>
    <div class="fyInfoReg">
        <div class="header">
            <my-banner>
                <img src="./img/宣传页面.png" srcset="./img/宣传页面@2x.png 2x, ./img/宣传页面@3x.png 3x" width="100%" alt="">
            </my-banner>
            <my-panel title="青羽会员登记">
                <div class="title">
                    <h4>亲爱的「赋羽青年」青羽会员</h4>
                </div>
                <div style="padding: 10px; color: #999; box-sizing: border-box; font-size: 14px;">
                    <p>感谢你选择「赋羽青年」为你的青春助力！</p>
                    <p>完成所有的资料的填写需要大概10分钟的时间，请你挑选一整读段时间认真填写，谢谢！</p>
                    <p>我们将会依据你所填的资料为你推荐导师及活动，你心仪的导师也将依据本资料进行学徒遴选。</p>
                    <p>如果你的信息发生更改，请及时重新填写。</p>
                </div>
            </my-panel>
        </div>
        <div class="content">
            <form ref="form">
                <input type="hidden" name="stuOpenID" v-model="openID">
                <div class="form-group">
                    <p class="form-title">
                        学生姓名 <img src="./img/必填.png" srcset="./img/必填@2x.png 2x, ./img/必填@3x.png 3x" alt="">
                    </p>
                    <input type="text" name="stuName" v-model="stuName" class="form-input" ref="stuName" @blur="_stuName()">
                </div>
                <div class="form-group">
                    <p class="form-title">
                        微信号 <img src="./img/必填.png" srcset="./img/必填@2x.png 2x, ./img/必填@3x.png 3x" alt="">
                    </p>
                    <input type="text" name="waChat" v-model="waChat" class="form-input" ref="waChat" @blur="_waChat()">
                </div>
                <div class="form-group">
                    <p class="form-title">
                        手机号 <img src="./img/必填.png" srcset="./img/必填@2x.png 2x, ./img/必填@3x.png 3x" alt="" >
                    </p>
                    <input type="tel" maxlength="11" name="phone" class="form-input" v-model="phone" ref="phone" @blur="_phone()">
                </div>
                <van-collapse v-model="activeNames" accordion>
                    <van-collapse-item title="完善更多资料" name="1">
                        <hr>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                性别
                            </p>
                            <div class="stuSex">
                                <p>
                                    <input type="radio" value="0" name="stuSex" id="man" class="form-input" v-model="stuSex" ref="stuSex" @change="sub()">
                                    <label for="man">男</label>
                                </p>
                                <p>
                                    <input type="radio" value="1" name="stuSex" id="woman" class="form-input" v-model="stuSex" ref="stuSex" @change="sub()">
                                    <label for="woman">女</label>
                                </p>
                            </div>
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                年级专业
                                <span class="sm-title">或所在部门</span>
                            </p>
                            <input type="text" name="stuDept" class="form-input" v-model="stuDept" ref="stuDept" @blur="sub()">
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                你所在城市
                                <span class="sm-title">如有更改，请及时更新</span>
                            </p>
                            <input type="text" name="stuCity" class="form-input" v-model="stuCity" ref="stuCity" @blur="sub()">
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                所属学校全称
                                <span class="sm-title">或所在机构/公司</span>
                            </p>
                            <input type="text" name="stuOrg" class="form-input" v-model="stuOrg" ref="stuOrg" @blur="sub()">
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                身份
                                <span class="sm-title">在校学生/职场人士</span>
                            </p>
                            <input type="text" name="identity" class="form-input" v-model="identity" ref="identity" @blur="sub()">
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                出生日期
                            </p>
                            <input type="date" name="birthday" class="form-input" v-model="birthday" ref="birthday" @blur="sub()">
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                给出自己五个最主要的正向标签
                                <span class="sm-title">如学霸、努力、时尚达人、好学、悟性高等</span>
                            </p>
                            <textarea name="stuTag" v-model="stuTag" @blur="sub()" ref="stuTag" class="allVal"></textarea>
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                给出自己三个最主要的负面标签
                                <span class="sm-title">如时间观念不强、不专注、健忘等</span>
                            </p>
                            <textarea name="stuTag2" v-model="stuTag2" @blur="sub()" ref="stuTag2" class="allVal"></textarea>
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                个人技能
                                <span class="sm-title">如文案、公众号运营、摄影、平面设计等技能进行简单的描述</span>
                            </p>
                            <textarea name="stuKill" v-model="stuKill" @blur="sub()" ref="stuKill" class="allVal"></textarea>
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                主要工作/学习经历
                            </p>
                            <textarea name="stuExperience" v-model="stuExperience" @blur="sub()" ref="stuExperience" class="allVal"></textarea>
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                10年后的自己
                                <span class="sm-title">10年后，怎样的自己，是你觉得满意的。</span>
                            </p>
                            <textarea name="giveAuto" v-model="giveAuto" @blur="sub()" ref="giveAuto" class="allVal"></textarea>
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                你理想中的人生导师
                                <span class="sm-title">描述，或举例</span>
                            </p>
                            <textarea name="giveTeacher" v-model="giveTeacher" @blur="sub()" ref="giveTeacher" class="allVal"></textarea>
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                对你未来的人生导师说一句话
                            </p>
                            <textarea name="expectTeacher" v-model="expectTeacher" @blur="sub()" ref="expectTeacher" class="allVal"></textarea>
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                你想在赋羽青年获得什么？ 
                            </p>
                            <div class="label-check" style="font-size: 12px;">
                                <p v-for="(item, index) in getSomethingArr" :key="index">
                                    <input type="checkbox" name="harvestList" :value="item.val" v-model="getSomethingArrVal" :id="'har'+index" @change="sub()">
                                    <label :for="'har'+index">{{item.val}}</label>
                                </p>
                            </div>
                            <div class="others">
                                <label for="">其他</label>
                                <input type="text" name="harvestList" v-model="harOthers" style="width: 80%;" @blur="sub()">
                            </div>
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                从哪里了解到赋羽青年？  
                            </p>
                            <div class="label-check" style="font-size: 12px;">
                                <p v-for="(item, index) in konwAboutFY" :key="index">
                                    <input type="checkbox" name="know" :value="item.val" v-model="knowArr" :id="'know'+index" @change="sub()">
                                    <label :for="'know'+index">{{item.val}}</label>
                                </p>
                            </div>
                            <div class="others">
                                <label for="">其他</label>
                                <input type="text" name="know" v-model="knowOther" style="width: 80%;" @blur="sub()">
                            </div>
                        </div>
                        <div class="form-group" style="color: #333; font-size: 14px;">
                            <p class="form-title">
                                其他补充信息
                                <span class="sm-title">可上传个人照片或简历等</span>
                            </p>
                            <div class="upFile">
                                <div class="files">
                                    <input type="file" name="file" @change="previewFiles($event)" multiple accept="image/*" ref="imgFile">
                                    <i class="iconfont icon-jiahao"></i>
                                    <p class="up-tips">请选择文件，限制10MB以内</p>
                                </div>
                                <div class="imgShow" v-if="imgIsShow">
                                    <div class="imgs">
                                        <img :src="imgPath" alt="" width="25%">
                                    </div>
                                    <p style="text-align: center;"><span class="imgDel" style="color: red;" @click="imgDel">删除</span> ，<span @click="imgRest">重新选择</span> </p>
                                </div>
                            </div>
                        </div>
                    </van-collapse-item>
                </van-collapse>
            </form>
            <div class="sub">
                <button @click="btnSub()">提交</button>
            </div>
        </div>
    </div>
</template>

<script>
import { Collapse, CollapseItem, Toast } from 'vant'
import myPanel from 'common/myPanel'
import myBanner from 'common/myBanner'
import tool from 'tool/tool'
export default {
    name: 'fyInfoReg',
    components: {
        myPanel,
        myBanner
    },
    data () {
        return {
            openID: '', //
            userState: '', // 用户状态
            activeNames: 1,
            stuName: '', // 学生姓名
            waChat: '', // 微信号
            phone: '', // 手机号
            stuSex: 0, // 性别
            stuDept: '', // 专业或部门
            stuCity: '', // 所在城市
            stuOrg:'', // 所在机构
            identity: '',  //身份
            birthday: '', // 出生日期
            stuTag: '', // 正向标签
            stuTag2: '', // 负面标签
            stuKill: '', // 个人技能
            stuExperience: '', // 经历
            giveAuto: '', // 十年后的自己
            expectTeacher: '', // 理想中的导师
            giveTeacher: '', // 给导师的一句话
            getSomethingArr: [
                {val:'职场通识'},
                {val:'人生方向'},
                {val:'人生导师'},
                {val:'工作技能'},
                {val:'实习工作机会'},
                {val:'实习证明'},
                {val:'认知自己'},
                {val:'扩展人脉'},
                {val:'增长见识'}
            ],
            konwAboutFY: [
                {val:'朋友介绍'},
                {val:'线上咨询'},
                {val:'线下活动'},
                {val:'荔枝微课'},
            ],
            getSomethingArrVal: [],
            knowArr: [],
            harOthers: '',
            knowOther: '',
            imgPath: '' , // 图片路径
            imgIsShow: false,  
            file: '',
        }
    },
    methods: {
        test (activeNames) { 
            console.log(activeNames)
        },
        _stuName () {  // 学生姓名
            if (this.stuName) {
                this.$refs.stuName.className = ''
                this.sub()
            } else {
                this.$refs.stuName.className = 'err'
                Toast('亲爱的会员，这是必填项请填写完整')
                return
            }
        },
        _waChat () {
            if (this.waChat) {
                if (this.waChat.length >= 6) {
                    this.$refs.waChat.className = ''
                    this.sub()
                }
            } else {
                Toast('亲爱的会员，这是必填项请填写完整')
                this.$refs.waChat.className = 'err'
                return
            }
        },
        _phone() { // 电话
            if (this.phone) {
                var reg = /^1[3|4|5|7|8|9][0-9]\d{4,8}$/
                if (reg.test(this.phone)) {
                    this.$refs.phone.className = ''
                    this.sub()
                } else { 
                    this.$refs.phone.className = 'err'
                    Toast('手机号码格式有误')
                    return
                }
            } else {
                this.$refs.phone.className = 'err'
                Toast('亲爱的会员，这是必填项请填写完整')
                return
            }
            
        },
        previewFiles (e) {  // 文件上传
            const files = e.target.files[0]
            const reader = new FileReader()
            const imgSize = 1024 * 1024 * 10 // 10MB
            if (files) {
                // 检查图片类型
                if(['jpeg', 'png', 'gif', 'jpg'].indexOf(files.type.split("/")[1]) < 0){
                    Toast.fail("文件类型仅支持 jpeg/png/gif！", 2000, undefined, false);
                    return;
                }
                // 文件大小自定义限制
                if(files.size > imgSize ) {
                    Toast.fail("文件大小不能超过10MB！", 2000, undefined, false);
                    return;
                }

                // 1. 读取图片
                reader.readAsDataURL(files)
                // 2. 读取完成
                reader.onloadend = (e) => {
                    this.imgPath = e.target.result
                    this.imgIsShow = true
                    this.file = files
                }
            }
        },
        imgDel () {  // 删除图片
            this.file = ''
            this.imgPath = ''
            this.imgIsShow = false
            this.$refs.imgFile.value = ''
        },
        imgRest () { // 重新选择
            this.file = ''
            this.imgPath = ''
            this.imgIsShow = false
            this.$refs.imgFile.value = ''
        },
        sub (key, val) {
            Toast.loading({
				mask: true,
				message: '提交中。。。'
			})
            let formData = new FormData(this.$refs.form)
            formData.append(key || null, val || null)
            this.$server.upStuInfo(formData).then((res) => {
                if (res == 'ok') {
                    Toast('提交成功')
					setTimeout(()=> {
						Toast.clear()
					}, 1000)
                    console.log(res)
                }
            })
            
        },
        btnSub () {
            if (!this.stuName) {
                Toast('亲爱的会员，必填项请填写完整')
                return
            } else if (!this.waChat) {
                Toast('亲爱的会员，必填项请填写完整')
                return
            } else if (!this.phone) {
                Toast('亲爱的会员，必填项请填写完整')
                return
            } else {
                this.sub('subFlag', 1)
                this.$router.push({path: '/user/fyQRCode', query: {flag:0}})
            }
        }
    },
    created () {
        if (localStorage.getItem('stuOpenID') || tool.getUrlKey('openID')) {
            this.openID = localStorage.getItem('stuOpenID') || tool.getUrlKey('openID')
        } else {
            const toast = Toast.loading({
                duration: 0, 
                forbidClick: true,
                loadingType: 'spinner',
                message: ''
            });

            let second = 3;
            const timer = setInterval(() => {
                second--;
                toast.message = '用户信息获取失败'
                if (!second) {
                    clearInterval(timer);
                    Toast.clear();
                }
            }, 1000)
            return
        }
        var getInfoParams = {
            openID: this.openID
        }
        // 验证用户状态
        this.$server.getIsDataIntegrity(getInfoParams).then((res) => {
            // 验证是否是会员
            if (res.vipFlag == false) {
                Toast('请先开通会员')
                this.$router.push({path:'/user/fyPay', query: {openID: this.openID}})
                return
            } else if (res.subFlag == true) {
                Toast('您已经成功报名，请记得扫码进群哦')
                this.$router.push({path: '/user/fyQRCode', query: {flag:0}})
                return
            }
        })
        
        
        // 获取用户数据
        this.$server.getStuInfoByOpenID(getInfoParams).then((res) => {
            if (res) {
                this.stuName = res.stuName
                this.waChat = res.waChat
                this.phone = res.phone
                if (res.stuSex) {
                    this.stuSex = res.stuSex?1:0
                }
                this.stuDept = res.stuDept
                this.stuCity = res.stuCity
                this.stuOrg = res.stuOrg
                this.identity = res.identity
                if (res.birthday) {
                    this.birthday = res.birthday.substr(0, 10)
                }
                this.stuTag = res.stuTag
                this.stuTag2 = res.stuTag2
                this.stuKill = res.stuKill
                this.stuExperience = res.stuExperience
                this.giveAuto = res.giveAuto
                this.expectTeacher = res.expectTeacher
                this.giveTeacher = res.giveTeacher
                if (res.harvestList) {
                    var testTxt = res.harvestList.split(',')
                    this.harOthers = testTxt[testTxt.length - 1]
                    this.getSomethingArrVal = res.harvestList.split(',')
                }
                if (res.know) {
                    var testTxt1 = res.know.split(',')
                    this.knowOther = testTxt1[testTxt1.length - 1]
                    this.knowArr = res.know.split(',')
                }
            }
        })
    }
}
</script>

<style scoped>
.content {
    background: #fff;
    padding-bottom: 45px;
}
.content form {
    font-size: 14px;
    padding: 20px;
    box-sizing: border-box;
}
.form-group {
    margin-bottom: 15px;
}
.form-group .form-title {
    margin: 5px 0px;
}
.form-group input[type=text],
.form-group input[type=tel],
.form-group input[type=date]
{
    width: 100%;
    box-sizing: border-box;
    padding: 6px 12px;
    border: 1px solid #999;
    border-radius: 3px;
}
.form-group textarea {
    width: 100%;
    height: 150px;
    border: 1px solid #999;
    border-radius: 3px;
    box-sizing: border-box;
    padding: 6px 12px;
    resize: none;
}
.sub {
    width: 100%;
    height: 45px;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.sub button {
    width: 100%;
    height: 100%;
    border: none;
    font-size: 16px;
    background: #dcb676;
    color: #6c5329;
}
.form-group input.err {
    border: 1px solid red;
}
.stuSex {
    display: flex;
}
.stuSex p {
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.stuSex input[type=radio] {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: url("./img/未选中-性别.png") no-repeat;
    background-position: 100%;
    -webkit-appearance: none;
    border: none;
    outline: none;
}
.stuSex input[type=radio]:checked {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: url("./img/选中-性别.png") no-repeat;
    background-position: 100%;
    -webkit-appearance: none;
    border: none;
    outline: none;
}
.stuSex p label {
    width: 100%;
    margin-left: 10px;
}
span.sm-title {
    display: block;
    font-size: 12px;
    color: #999;
    margin-top: 3px;
    margin-bottom: 5px;
}
.label-check {
    display: flex;
    flex-flow: wrap;
}
.label-check p {
    width: 33.33%;
    margin: 5px 0px;
}
.label-check input[type=checkbox] {
    width: 20px;
    height: 20px;
    border-radius: 5px;
    background: url("./img/未选中.png") no-repeat;
    background-position: 100%;
    -webkit-appearance: none;
    border: none;
    outline: none;
}
.label-check input[type=checkbox]:checked {
    width: 20px;
    height: 20px;
    border-radius: 5px;
    background: url("./img/选中.png") no-repeat;
    background-position: 100%;
    -webkit-appearance: none;
    border: none;
    outline: none;
}
.upFile {
    position: relative;
}
.files {
    width: 100%;
    height: 180px;
    position: relative;
    border: 2px dashed #999;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.files input[type='file'] {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
}
.files i {
    font-size: 38px;
    color: #999;
}
.upFile .imgShow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.imgShow .imgs {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
}
</style>



