<template>
    <div class="check">
        <div class="title">
            <p>您已选择</p>
        </div>
        <div class="content">
            <img :src="'img/'+checkedTeacher.pic" width="70%" alt="">
            <p class="teacher-name">{{checkedTeacher.teaName}}</p>
            <p class="tips">请静心等待导师选择</p>
        </div>
        <div class="footer">
            <button class="next" @click="next">下一步</button>
        </div>
    </div>
</template>

<script>
import {Toast} from 'vant'
export default {
    name: 'check',
    data () {
        return {
            checkedTeacher: {}
        }
    },
    methods: {
        next () {
            var paramsObj = {
                stuOpenID: this.$route.query.stuOpenID || JSON.parse(sessionStorage.getItem('userInfo')).openID
            }
            this.$server.getChooseByStuID(paramsObj).then((res) => {
                console.log(res)
                if (res != undefined) {
                    if (res.flag == 0 || res == '') {
                        Toast('请耐心等待导师的选择')
                        return 
                    } else if (res.flag == 1) {  // 导师面试群
                        this.$router.push({path: '/user/qrCode', query: {flag: 2}})
                    } else if (res.flag == 2) {  // 赋羽私塾群
                        this.$router.push({path: '/user/qrCode', query: {flag: 1}})
                    } else if (res.flag == 3){ // 导师未选择
                        this.$router.push({path: '/user/noCheck', query: {stuOpenID: this.$route.query.stuOpenID, teaOpenID: this.$route.query.teaOpenID}})
                    } 
                } 
            })
        }
    },
    created () {
        // 获取已选择导师信息
        var paramsObj = {
            openID: this.$route.query.teaOpenID
        }
        this.$server.getTeacherCard(paramsObj).then((res) => {
            if (res) {
                this.checkedTeacher = res
            }
        })
    }
}
</script>

<style scoped>
.check {
    width: 100%;
    height: 100%;
    background: #f5f5f5;
}
.title {
    text-align: center;
    font-size: 16px;
    color: #999;
    box-sizing: border-box;
    padding: 5px;
}
.content {
    box-sizing: border-box;
    padding: 20px;
    text-align: center;
    font-size: 16px;
    color: #999;
}
.content p {
    padding: 10px;
}
.footer {
    font-size: 14px;
    text-align: center;
    cursor: pointer;
}
.next {
    padding: 6px 30px;
    color: #fff;
    background: #1989fa;
    border: none;
    outline: none;
    border-radius: 4px;
}
</style>



