<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
html, body {
  width: 100%;
  height: 100%;
}
  /*解决移动端滑动不顺畅问题*/
body{
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
  overflow-y: scroll;
}
  #app {
    width: 100%;
    height: 100%;
    background: #f5f5f5;
  }
</style>



