<template>
    <div class="fyCheckMentor">
        <van-swipe @change="onChange">
            <van-swipe-item v-for="(item, index) in teacherCard" :key="index">
                <router-link :to="{path:'/user/teacherDetail',query:{teaOpenID:item.teaOpenID, richTextID: item.richTextID}}">
                    <img :src="'img/'+item.pic" width="80%" alt="">
                </router-link>
            </van-swipe-item>
        </van-swipe>
        <div class="check-now">
            <button type="button" @click="checkNow">立刻选择导师</button>
        </div>
    </div>
</template>

<script>
import { Swipe, SwipeItem, Toast} from 'vant'
import tool from 'tool/tool'
export default {
    name: 'fyCheckMentor',
    data () {
        return {
            teachIndex: 0,
            teacherCard: [],
            stuOpenID: ''
        }
    },
    methods: {
        onChange (index) {
            this.teachIndex = index
        },
        checkNow () {
            this.$router.push({path:'/user/teacherDetail',query:{stuOpenID: this.stuOpenID,teaOpenID: this.teacherCard[this.teachIndex].teaOpenID, richTextID:this.teacherCard[this.teachIndex].richTextID}})
        }
    },
    created () {
        this.$server.getTeacherCardList().then((res) => {
            console.log(res)
            if (res) {
                this.teacherCard = res
            }
        })
        // 获取学生openID
        if (localStorage.getItem('stuOpenID')) {
            this.stuOpenID = localStorage.getItem('stuOpenID')
        } else {
            this.stuOpenID = tool.getUrlKey('openID')
        }
    }
}
</script>

<style scope>
.van-swipe-item {
    text-align: center;
}
.check-now {
    width: 100%;
    height: 40px;
    position: fixed;
    bottom: 0;
    left: 0;
    font-size: 16px;
}
.check-now button {
    width: 100%;
    height: 100%;
    background: #dcb676;
    color: #6c5329;
    border: none;
    outline: none;
}
</style>



